<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center mb-base">
      <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
      <vs-button class="p-3 mr-4" @click="modify" color="primary" type="filled" icon="fa-pen" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('MODIFY') }}</vs-button>
    </div>
    <div class="row hover mb-base">
      <p class="info-row">
        <span>{{ $t('ID') }}:</span>
        <span>{{ template.templateId }}</span>
      </p>
      <p class="info-row">
        <span>{{ $t('TEMPLATE_NAME') }}:</span>
        <span>{{ template.templateName }}</span>
      </p>
      <p class="info-row">
        <span>{{ $t('ALERT_TYPE') }}:</span>
        <span>{{ template.alertTypeDescription | placeholder('-')  }}</span>
      </p>
      <p class="info-row">
        <span>{{ $t('CREATION_DATE') }}:</span>
        <span>{{ template.creationDate | formatUnixTimestamp }}</span>
      </p>
    </div>

    <vs-tabs>
      <vs-tab :label="$t('BODY')">
        <codemirror class="code-editor" v-model="template.body" :options="vueOptions"></codemirror>
      </vs-tab>
      <vs-tab :label="$t('STYLE')" v-if="template.alertTypeEnumId === 'ALERT_EMAIL'">
        <codemirror class="code-editor" v-model="template.style" :options="cssOptions"></codemirror>
      </vs-tab>
    </vs-tabs>
  </nv-card>
</template>

<script>
import CodeEditorMixin from '@/mixins/commons/CodeEditorMixin'
import AlertTemplateService from '@/services/commons/AlertTemplateService'

export default {
  name: 'alert-template-view',
  mixins: [CodeEditorMixin],
  data: function () {
    return {
      template: {}
    }
  },
  methods: {
    async load () {
      let self = this
      AlertTemplateService.alertTemplates({
        templateId: this.$route.params.id
      }).then(function (response) {
        self.template = response.data.alertTemplateList[0] || {}
      })
    },
    cancel () {
      this.$router.back()
    },
    modify () {
      this.$router.push({ name: 'ModifyAlertTemplate', params: { id: this.$route.params.id } })
    }
  },
  mounted () {
    this.load()
  }
}
</script>
