import Endpoint from '@/services/Endpoint'

export default {
  alertTemplates (payload) {
    return Endpoint().get('/s1/commons/basic/alertTemplate', {
      params: payload
    })
  },
  createAlertTemplate (payload) {
    return Endpoint().post('/s1/commons/basic/alertTemplate', payload)
  },
  modifyAlertTemplate (payload) {
    return Endpoint().patch('/s1/commons/basic/alertTemplate', payload)
  }
}
